// little advertisement for the new feature
// only shows if jam mode is off and not on mobile
// this gets deleted when jam mode turns off in toggleJamMode() function
// just for demo purposes

export default class DoubleClickToCommentInstructionBanner extends React.Component {
    state = { doneShowingBanner: false }

    componentDidMount = () => {
        setTimeout(() => { 
            this.setState({ doneShowingBanner: true })
        }, 3000);
    }

    render() {
        const { doneShowingBanner } = this.state

        if (doneShowingBanner) {
            return (
                <div className="jam---double-click-to-comment-instruction-banner">
                    <p>Type <span className="jam---code">jam</span> to enable Jam</p>
                </div>
            )
        } else {
            return <div></div>
        }
    }
}